'use client';
import React, { useEffect } from 'react';
import dynamic from 'next/dynamic';
import { motion } from 'framer-motion';
import styles from './dashboard.module.css';

// Hooks
import { useTrips } from '@/hooks/useTrips';
import { useDashboardAuth } from '@/hooks/useDashboardAuth';

// Components
import { DashboardHeader } from '@/components/dashboard/DashboardHeader';
import { AIQuerySection } from '@/components/dashboard/AIQuerySection';
import { AddTripForm } from '@/components/dashboard/AddTripForm';
import { TripList } from '@/components/dashboard/TripList';
import Onboarding from '@/components/Onboarding';
import { DashboardSkeleton } from '@/components/dashboard/DashboardSkeleton';

// Dynamic Import
// Dynamic Import with intentional delay
const GridScan = dynamic(() => import('@/components/GridScan').then(mod => mod.GridScan), {
    ssr: false,
});

export default function DashboardPage() {
    const {
        isChecking,
        showOnboarding,
        username,
        userId,
        handleLogout,
        handleOnboardingComplete
    } = useDashboardAuth();

    const {
        trips,
        fetchTrips,
        addTrip,
        deleteTrip,
        updateTrip
    } = useTrips();

    const [shouldLoadBg, setShouldLoadBg] = React.useState(false);

    useEffect(() => {
        // Delay background heavy load to prioritize interactivity
        const timer = setTimeout(() => setShouldLoadBg(true), 100);
        return () => clearTimeout(timer);
    }, []);

    // Fetch trips once user is identified
    useEffect(() => {
        if (userId) {
            fetchTrips(userId);
        }
    }, [userId, fetchTrips]);

    if (isChecking) {
        return <DashboardSkeleton />;
    }

    return (
        <div className={styles.dashboardBg}>
            {showOnboarding ? (
                <Onboarding onComplete={handleOnboardingComplete} />
            ) : (
                <>
                    {/* GridScan Background Effect - Delayed Load */}
                    {shouldLoadBg && (
                        <div style={{
                            position: 'fixed',
                            top: 0,
                            left: 0,
                            width: '100vw',
                            height: '100vh',
                            zIndex: 0,
                            pointerEvents: 'none',
                            opacity: 0,
                            animation: 'fadeInGrid 1s ease-in-out forwards',
                            contentVisibility: 'auto'
                        }}>
                            <style jsx>{`
                                @keyframes fadeInGrid {
                                    from { opacity: 0; }
                                    to { opacity: 1; }
                                }
                            `}</style>
                            <GridScan
                                sensitivity={0.7}
                                lineThickness={1}
                                linesColor="#392e4e"
                                gridScale={0.1}
                                scanColor="#9eecff"
                                scanOpacity={0.7}
                                lineJitter={0.1}
                                scanGlow={0.5}
                                scanSoftness={2}
                                scanDuration={5.0}
                                scanDelay={1.0}
                                enablePost={true}
                                bloomIntensity={1.2}
                                chromaticAberration={0.002}
                                noiseIntensity={0.01}
                                enableWebcam={false}
                                showPreview={false}
                                style={{ width: '100%', height: '100%' }}
                            />
                        </div>
                    )}

                    {/* Dashboard Content */}
                    <motion.div
                        initial={{ opacity: 0, y: 10 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.2, ease: "easeOut" }} // Even faster entry
                        style={{ position: 'relative', zIndex: 1 }}
                    >
                        {/* Animated light streaks */}
                        <div className={styles.lightStreak1}></div>
                        <div className={styles.lightStreak2}></div>

                        <DashboardHeader username={username} onLogout={handleLogout} />

                        <div className={styles.container}>
                            <AIQuerySection trips={trips} />

                            <AddTripForm userId={userId} onTripAdded={(newTrip) => addTrip({ ...newTrip } as any)} />

                            <TripList
                                trips={trips}
                                onDeleteTrip={deleteTrip}
                                onUpdateTrip={updateTrip}
                            />
                        </div>
                    </motion.div>
                </>
            )}
        </div>
    );
}
